import React, { useState, useEffect, useRef, } from "react";
import io from "socket.io-client";
import { RoomAPI, OnRoomStart, OnRoomEnd, OnSnapThumbnail, OnSaveRecordVideo, GetRoomInfo } from '../../store/RoomController'
import { GetUserInfo, } from '../../store/CredentialController'
import { CONNECTION_PORT, ROOM_SAVE_RECORD_SNAP, } from '../../utils/Urls'
import { playDonateSound, } from '../controls/Sound'
import { FaRegEye, FaHandPointRight, FaClock, } from "react-icons/fa";
import Peer from 'peerjs';
import { showToast } from '../controls/Toast'
import { showLoading } from '../controls/Loading'
import Page404 from '../controls/Page404'
import { Modal } from "react-responsive-modal";
import { useDispatch, } from 'react-redux';
import makeAnimated from 'react-select/animated';
import Select from 'react-select'
import { ChatBox } from './chat/ChatBox';
import DEFAULT_THUMBNAIL from '../../assets/img/3.png'
import { delayFunc, randColor, } from '../../utils/Helper';
import videojs from 'video.js'
import { Beforeunload } from 'react-beforeunload';
import { useHistory } from 'react-router-dom'
import moment from 'moment'
import 'videojs-record/dist/videojs.record.js';
import 'videojs-record/dist/plugins/videojs.record.ts-ebml.js';

const animatedComponents = makeAnimated();

let getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia;
let socket, local_stream, player, snapInternal, peer

function Streamer() {
    const [chatBox, setChatBoxState] = useState(true)
    const [inputValues, setInputValues] = useState({})
    const [isLoading, setIsLoading] = useState(true);
    const [userInfo, setUserInfo] = useState({})
    const [tags, setTags] = useState([])
    const [roomTags, setRoomTags] = useState([])
    const [_, setMutate] = useState(false)
    const [viewers, setViewers] = useState(0)
    const [isPlayedVideo, setIsPlayedVideo] = useState(false)
    const [room, setRoom] = useState({})
    const [loggedIn, setLoggedIn] = useState(true)
    const dispatch = useDispatch();
    const videoStatus = useRef(0)
    const roomInfo = useRef({})
    const [visibleModal, setVisibleModal] = useState(false)
    const [isReadyToRecord, setReadyToRecordState] = useState(false)
    const [isFinishRecord, setFinishRecordState] = useState(false)
    const history = useHistory()
    const avatarRef = useRef(null)

    const onChangeForm = (value, stateName) => {
        inputValues[stateName] = value
        setRoomTags(value)
        setMutate(e => !e)
    }
    const setLocalStream = (stream) => {
        let snapVideo = document.getElementById("snap-video");
        snapVideo.srcObject = stream;
        snapVideo.play().then(snap)
    }
    const onStartStream = async (info) => {
        try {
            const { nickname, id, } = info
            const { description, title, tags, } = inputValues
            const roomData = {
                user_id: id,
                title: title || 'title',
                description: description || 'description',
                tags: tags ? JSON.stringify(tags) : '[]',
                thumbnail: '',
            }
            const result = await OnRoomStart(roomData)
            if (result.status === 200) {
                peer = new Peer(nickname)
                peer.on('open', (id) => {
                    try {
                        console.log("Peer Connected with ID: ", id)
                        showToast('info', 'Your image is being streaming!')
                        setIsPlayedVideo(true)
                        socket.emit("open_room", { nickname })
                        roomInfo.current = result?.data
                        setMutate(e => !e)
                        getUserMedia({ video: true, audio: false }, (stream) => {
                            local_stream = stream;
                            setLocalStream(local_stream)
                        }, (e) => {
                            showToast('error', e)
                        })
                    } catch (e) {
                        console.log(e)
                        onEndStream()
                    }
                })
                peer.on('call', (call) => {
                    call.answer(local_stream);
                })
                peer.on('connection', function (conn) {
                    console.log(conn, 'connection')
                })
            } else {
                showToast("error", "Error start stream!")
                onEndStream()
            }
        } catch (e) { showToast("error", e); onEndStream() }
    }
    const onEndStream = async () => {
        const { id, streamer, streamer_id, } = roomInfo.current || {}
        const data = {
            room_id: id,
            streamer_id: streamer_id,
            status: 0,
        }
        await OnRoomEnd(data)
        const msgToast = `User ${streamer} has ended the live stream`
        const msgChatbox = `User ${streamer} has ended the live stream`
        socket.emit("on_donate", { streamer, message: msgToast, isDonate: false, })
        socket.emit('sendMessage', { message: msgChatbox, userChat: streamer, roomName: streamer, colorRand: randColor(), createdAt: moment() });
    }
    const onImageChange = (event) => {
        if (event.target.files && event.target.files[0]) {
            inputValues.thumbnail = event.target.files[0]
            setMutate(e => !e)
        }
    }
    const snap = async () => {
        const info = await GetUserInfo()
        try {
            snapInternal = setInterval(() => {
                const video = document.getElementById("snap-video");
                const canvas = document.createElement("canvas");
                canvas.width = 500;
                canvas.height = 500;
                canvas.getContext('2d').drawImage(video, 0, 0, canvas.width, canvas.height);
                canvas.toBlob((blob) => {
                    const reader = new FileReader();
                    reader.readAsDataURL(blob);
                    reader.onloadend = function () {
                        const base64String = reader.result;
                        const data = {
                            fileName: `${info.id}_${info.nickname}.png`,
                            imageBase64: base64String,
                            user_id: info.id,
                        }
                        OnSnapThumbnail(data)
                    }
                })
            }, 2000);
        } catch (e) { console.log(e) }
    }
    const callEffect = async () => {
        showLoading(true)
        setIsLoading(true)
        try {
            // * Info
            const info = await GetUserInfo()
            setUserInfo(info)
            if (info.id === undefined) {
                setLoggedIn(false)
                showLoading(false)
                setIsLoading(false)
                return
            }
            // * Role Checking
            if (info.role_user === "VIEWER") {
                window.location.href = "/"
                showLoading(false)
                setIsLoading(false)
                return
            }
            const tags = await dispatch(RoomAPI.GetRoomTags());
            setTags(tags?.data || [])
            const roomInfo = await GetRoomInfo({ roomName: info.nickname, idUser: info.id })
            setRoomTags(JSON.parse(roomInfo?.data?.tags) || [])
            setRoom(roomInfo?.data || {})
            // * Socket
            socket = io(CONNECTION_PORT);
            socket.emit("open_donate_room", { nickname: info.nickname })
            socket.on('message_donate', delayFunc(({ message, isDonate, }) => {
                if (isDonate) {
                    showToast('dark', message, { position: "bottom-center" })
                    playDonateSound()
                }
            }))
            socket.on('update_viewer_watching', ({ currentViewers }) => {
                setViewers(currentViewers)
            })
        } catch (e) { console.log(e) }
        showLoading(false)
        setIsLoading(false)
    }
    const updateVideoInfo = async () => {
        if (!inputValues.title || !inputValues.thumbnail) return
        showLoading(true)
        try {
            const userInfo = await GetUserInfo();
            const param = {
                userInfo: userInfo,
                title: inputValues.title,
                recordData: player.recordedData,
            }
            player.record().reset()
            const result = await OnSaveRecordVideo(param)
            if (result.status === 200) {
                const reader = new FileReader();
                reader.readAsDataURL(inputValues.thumbnail);
                reader.onload = async function () {
                    const options = {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json',
                        },
                        body: JSON.stringify({
                            user_id: userInfo.id,
                            fileName: inputValues?.thumbnail?.name || '',
                            imageBase64: reader.result,
                        }),
                    };
                    await fetch(ROOM_SAVE_RECORD_SNAP, options)
                    showToast('success', result.msg)
                    setVisibleModal(false)
                };
            } else {
                showToast('error', result.msg)
            }
        } catch (e) { console.log(e) }
        showLoading(false)
    }
    const closeRoomInfoModal = async (isFinishRecord) => {
        if (isFinishRecord) {
            player.record().reset()
        } else {
            onStartStream(await GetUserInfo());
            initVideo();
        }
        setVisibleModal(false);
        setReadyToRecordState(true);
    }
    const startStream = () => {
        setVisibleModal(true);
    }
    const initVideo = () => {
        try {
            const videoMaxLengthInSeconds = 60 * 60;
            player = videojs("local-video", {
                controls: true,
                fluid: true,
                plugins: {
                    record: {
                        audio: true,
                        video: true,
                        maxLength: videoMaxLengthInSeconds,
                        convertEngine: 'ts-ebml',
                    }
                }
            });
            player.on('error', function (error) {
                console.log('error:', error);
            });
            player.on('play', async function () {
                videoStatus.current = 1
                setMutate(e => !e)
            })
            player.on('pause', function () {
                console.log('pause')
                videoStatus.current = 0
                setMutate(e => !e)
            })
            player.on('suspend', function () {
                console.log('suspend')
                videoStatus.current = 0
                setMutate(e => !e)
            })
            player.on('abort', function () {
                console.log('abort')
                videoStatus.current = 0
                setMutate(e => !e)
            })
            player.on('finishRecord', async function () {
                setFinishRecordState(true)
                setVisibleModal(true)
            });
        } catch (e) { showToast('error', e) }
    }
    useEffect(() => {
        callEffect()
        return history.listen(() => {
            try {
                if (videoStatus.current === 1) {
                    onEndStream()
                    window.location.reload()
                }
                clearInterval(snapInternal)
                player.dispose()
            } catch (e) { }
        })
    }, [history]);
    return (
        !loggedIn && !isLoading ? (
            <Page404 text="Login Required" />
        ) : !isLoading && (
            <div className='container-fluid d-flex flex-row m-auto p-4' style={{ zIndex: 999, pointerEvents: 'auto', }}>
                {isPlayedVideo && (
                    <Beforeunload onBeforeunload={(event) => {
                        event.preventDefault()
                        onEndStream()
                        window.location.reload()
                    }} />
                )}
                <div className='w-100' style={{ borderRadius: 15 }}>
                    <div className='d-flex' style={{ position: "relative" }}>
                        <div style={{ display: 'flex', flexDirection: 'column', width: '100%', height: '100%', }}>
                            <div style={{ width: '100%', height: '100%' }}>
                                <div style={{ display: 'none' }}><video id="snap-video" /></div>
                                <video id="local-video" className="video-js" style={{ minWidth: '100%', minHeight: '80vh', maxHeight: '100%', margin: "auto" }} />
                            </div>
                            {isPlayedVideo && (
                                <div style={{ background: 'rgba(33, 33, 33, 0.5)', height: 130, padding: 20, paddingLeft: 20, marginTop: 20, borderRadius: 5, display: 'flex', flexDirection: 'column', justifyContent: 'center', }}>
                                    <span className="text_primary" style={{ fontSize: 19, }}><FaHandPointRight style={{ marginRight: 10, }} />{inputValues.title || "title"}</span>
                                    <span className="text_primary" style={{ fontSize: 19, }}><FaClock style={{ marginRight: 10, }} /> {moment(roomInfo?.current?.created_at).endOf('min').fromNow()}</span>
                                    <span className="text_primary" style={{ fontSize: 19, }}><FaRegEye style={{ marginRight: 10, }} /> {viewers}</span>
                                </div>
                            )}
                        </div>
                        {isReadyToRecord ? null :
                            <div className='px-3 d-flex flex-column w-100 h-100 align-items-center justify-content-center'
                                style={{ position: "absolute", top: 0, left: 0, }}>
                                <div className='p-2'>
                                    <div><button className='w-100 btn btn-outline-primary btn-lg' onClick={() => startStream()}>Start</button></div>
                                </div>
                            </div>
                        }
                        <div onClick={() => { setChatBoxState(!chatBox) }} className={chatBox ? 'collapse' : 'text-center mx-2 py-2 text-light'}
                            style={{ width: 30, height: 30, cursor: 'pointer', position: "absolute", right: 0, top: 0, opacity: 0.5 }}>
                            <div className='d-flex'>
                                <img className='m-auto' src={require("../../assets/img/page/show_right_side_panel_48px.png")}
                                    alt="" style={{ height: 30, width: 30 }} />
                            </div>
                        </div>
                    </div>
                </div>
                <div className={chatBox ? 'ml-3 shadow' : 'collapse'} style={{ position: 'relative', minWidth: 300, background: 'rgb(33,33,33,0.5)', borderRadius: 10 }}>
                    <div style={{ width: '100%', height: '100%', position: 'absolute', right: 0, top: 0 }}>
                        <div className='d-flex flex-column' style={{ position: 'relative', height: '100%' }}>
                            <div className='px-3'>
                                <div className='border-bottom w-100 d-flex justify-content-end py-2'>
                                    <div className='align-self-center'>
                                        <img className='m-auto' src={require("../../assets/img/page/show_left_side_panel_48px.png")}
                                            alt="" style={{ width: 28, cursor: "pointer" }}
                                            onClick={() => { setChatBoxState(!chatBox) }} />
                                    </div>
                                </div>
                            </div>
                            {(userInfo?.nickname) ? <ChatBox roomName={userInfo.nickname} userChat={userInfo.nickname} /> : <></>}
                        </div>
                    </div>
                </div>
                <Modal open={visibleModal} center onClose={() => closeRoomInfoModal()}>
                    <div className='row py-4 px-0' style={{ minWidth: '50vw', maxWidth: '50vw', }}>
                        <div className='py-3' style={{ width: '100%', display: 'flex', flexDirection: 'row', alignItems: 'space-around', justifyContent: 'space-around', }}>
                            <div className='w-75'>
                                <input className='border form-control' value={inputValues.title} placeholder="Title..."
                                    onChange={(e) => onChangeForm(e.target.value, 'title')} />
                            </div>
                        </div>
                        {isFinishRecord ?
                            (
                                <>
                                    <div className='py-3' style={{ width: '100%', display: 'flex', flexDirection: 'column', alignItems: 'space-around', justifyContent: 'space-around', }}>
                                        <div className='w-75 m-auto d-flex align-items-center'>
                                            <div className='d-flex' style={{ width: '30%' }}><h4>Thumbnail</h4></div>
                                            <div className='' style={{ width: '70%', display: 'flex', justifyContent: 'center', }}>
                                                <div style={{ position: 'relative', width: 300, height: 200, }}>
                                                    <img src={inputValues?.thumbnail ? URL.createObjectURL(inputValues?.thumbnail) : DEFAULT_THUMBNAIL} alt="" style={{ width: '100%', height: '100%', borderRadius: 10, objectFit: 'cover', }} />
                                                    <button onClick={() => avatarRef?.current?.click()} style={{ position: 'absolute', top: 0, left: 0, width: '100%', height: '100%', opacity: 0, }} />
                                                    <input ref={avatarRef} type="file" onChange={onImageChange} style={{ display: 'none', }} />
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </>
                            ) : (
                                <>
                                    <div className='py-3' style={{ width: '100%', display: 'flex', flexDirection: 'row', alignItems: 'space-around', justifyContent: 'space-around', }}>
                                        <div className='w-75'>
                                            <textarea className='border form-control' defaultValue={room.description}
                                                style={{ width: '100%', height: 100, }} onChange={(e) => onChangeForm(e.target.value, 'description')} placeholder="Description..." />
                                        </div>
                                    </div>
                                    <div className='py-3' style={{ width: '100%', display: 'flex', flexDirection: 'row', alignItems: 'space-around', justifyContent: 'space-around', }}>
                                        <div className='w-75'>
                                            <div style={{ width: '100%', }}>
                                                <Select options={tags} onChange={(e) => onChangeForm(e, 'tags')}
                                                    closeMenuOnSelect={false} components={animatedComponents}
                                                    isMulti placeholder='Tags...'
                                                    value={roomTags}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                </>
                            )
                        }
                        <div className='pt-3' style={{ width: '75%', display: 'flex', justifyContent: 'flex-end', margin: "auto" }}>
                            <div className='pl-1'><button className='btn btn-primary' onClick={() => closeRoomInfoModal(isFinishRecord)}>Cancel</button></div>
                            <div className='pl-1'><button className='btn btn-primary' onClick={() => isFinishRecord ? updateVideoInfo() : closeRoomInfoModal()}>Finish</button></div>
                        </div>
                    </div>
                </Modal>
            </div>
        )
    );
}
export default Streamer;
